.jf-button-container.button-block {
  margin-bottom: 1.5em; }

.jf-button-container.button-inline-block {
  display: inline-block; }

.jf-button-container.float-left {
  margin-right: 4px; }

.jf-button-container.float-right {
  margin-left: 4px; }

/**************************************************************/
/*  horizontal rule  */
hr,
hr.rule1,
hr.rule2,
hr.rule3 {
  border: 0;
  width: 100%;
  text-align: left;
  margin: 32px 0 30px;
  clear: both;
  height: 1px;
  color: #ccc;
  background-color: #ccc; }

hr.rule1 {
  color: #E32927;
  background-color: #E32927; }

hr.rule2 {
  color: #000;
  background-color: #000; }

/**************************************************************/
/* uses a counter (not supported in IE8) */
ol.counter-1,
ol.counter-1 ol {
  counter-reset: item; }

ol.counter-1 > li,
ol.counter-1 ol > li {
  counter-increment: item; }

ol.counter-1 ol > li {
  display: block; }

ol.counter-1 ol > li:before {
  content: counters(item, ".") ".";
  display: block; }

ol.no-counter-1,
ol.no-counter-1 li {
  list-style-type: none; }

ol.no-counter-1 {
  margin-left: .75em; }

/**************************************************************/
/* ordered list types */
ol.list-type-decimal {
  list-style-type: decimal; }

ol.list-type-lower-alpha {
  list-style-type: lower-alpha; }

ol.list-type-upper-alpha {
  list-style-type: upper-alpha; }

ol.list-type-lower-roman {
  list-style-type: lower-roman; }

ol.list-type-upper-roman {
  list-style-type: upper-roman; }

/**************************************************************/
/* TABLES!!! P.A.R.T.... why? Because I gotta! */
.css_table,
.css-table {
  display: table;
  width: 100%;
  border-collapse: collapse; }

.css_table_row,
.css-table-row {
  display: table-row; }

.css_table_cell,
.css-table-cell {
  display: table-cell;
  vertical-align: top; }

.css_table_cell.align_middle,
.css-table-cell.align-middle {
  vertical-align: middle; }

table {
  border-collapse: collapse;
  width: 100%;
  font-size: 14px;
  line-height: 1.45;
  border: none;
  margin-bottom: 2.5em;
  /* tables visually benefit from extra margin when above other elements */ }

main table {
  overflow-x: auto;
  /* display: block; why was this used? to enforce overflow for mobile? */ }

main .table_wrapper1 {
  overflow-x: auto; }

.admin-menu article table {
  display: table; }

table caption {
  padding: 7px 6px;
  font-size: 18px;
  text-align: center;
  caption-side: initial; }

table td,
table th,
table th label,
.calendar-calendar table thead th.days {
  padding: 11px 13px 10px;
  vertical-align: top; }

table th,
table thead th,
table thead td,
.calendar-calendar table thead th.days {
