  color: #fff; }

table th a:focus,
main table th a:focus {
  text-decoration: underline; }

table th.active img {
  display: none; }

table tr.odd,
table tr:nth-child(odd) {
  background-color: transparent; }

table tr.even,
table tr:nth-child(even) {
  background-color: #f9fafb; }

table tr.odd,
table tr.even,
table tr:nth-child(odd),
