table tr th:last-child {
  border-right: 1px solid #1e354a; }

table thead tr,
table thead tr.odd,
table thead tr.even,
table thead tr:nth-child(odd),
table thead tr:nth-child(odd) {
  border-bottom: 1px solid #1e354a; }

table tr td:first-child {
  border-left: 1px solid #ededed; }

table tr td:last-child {
  border-right: 1px solid #ededed; }

/* evenly spaced columns */
table.table--two-columns th,
table.table--two-columns td {
  width: 50%; }

table.table--three-columns th,
table.table--three-columns td {
  width: 33.3%; }

table.table--four-columns th,
table.table--four-columns td {
  width: 25%; }

table.table--five-columns th,
table.table--five-columns td {
  width: 20%; }

/* controlling the first column */
