  width: 33.3%; }

table.table--first-column-25pct th.table-column1,
table.table--first-column-25pct td.table-column1 {
  width: 25%; }

/* layout table... because */
table.layout-1 {
  border-collapse: collapse;
  margin: 0;
  width: auto;
  border-spacing: 0; }

table.layout-1 tbody {
  border: none; }

table.layout-1 td,
table.layout-1 th {
  padding: 0;
  vertical-align: top; }

table.layout-1 tr.odd,
table.layout-1 tr.even,
table.layout-1 tr td:first-child,
table.layout-1 tr td:last-child,
table.layout-1 tr:nth-child(odd),
